 import { Avatar } from "antd";
import "antd/dist/antd.css";
import { Anchor } from 'antd';
import { Switch } from 'antd';
import { useState } from "react";
 const { Link } = Anchor;

export const Navbar=()=>{
    const [theme,setTheme]=useState('pink')
    return <div id="Navbar">
         <div className="left"> 
         <Avatar size={45}>AS</Avatar>  
          </div>
         <div className="right">   
            <Anchor>
                <div className="right">
                <div><Link href="#Home" title="Home" /></div>
                <div> <Link href="#pro" title="Project" /></div>
                <div> <Link href="#SK" title="Skill" /></div>
                <div> <Link href="#Media" title="Contact" /></div>
                <Switch onChange={()=>{
                    setTheme(theme==='pink'?'dark':'pink')
                }} checkedChildren="Pink" unCheckedChildren="Dark" defaultChecked />

                </div>
               
              </Anchor>
             
           </div>
          
               
    </div>
}