import TextTransition, { presets } from "react-text-transition";
import React  from "react";
import { Image } from 'antd';
import AOS from 'aos';
import 'aos/dist/aos.css'; 
AOS.init();
const TEXTS = [
  "AMAN SHARMA",
  "FULL STACK DEVELOPER",
];

export const Home=()=>{
    const [index, setIndex] = React.useState(0);

    React.useEffect(() => {
      const intervalId = setInterval(() =>
        setIndex(index => index + 1),
        3000 // every 3 seconds
      );
      return () => clearTimeout(intervalId);
    }, []);
    return <div id="Home" data-aos="zoom-out-up">
        <div id="main">
            <div>  
        <h3>FULL STACK DEVELOPER | DELHI | (+91) 8810316635</h3>
        <p>Hi, Myself Aman Sharma. I am a full MERN stack developer.<br/> I am passaionate about building web application with JavaScript | React | Node.js | MongoDb and Frameworks </p>
</div>
            <div> 
                  <Image
      width={250}
      src="https://i.postimg.cc/MZdc3rZ6/amanpic.jpg"
    /> <h1> <TextTransition style={{
        marginLeft:"110px"
    }}
    text={ TEXTS[index % TEXTS.length] }
    springConfig={ presets.wobbly }
  /></h1></div>
        </div>
       
     
    </div>
}